import { useParams } from "react-router"
import moonlist from "./moonlist"
import sunlist from "./sunlist"
import planetlist from "./planetlist"
import './imageDetails.css'

//надо чтобы он для поиска выбирал тот массив, в котором есть этот ид
//если не указано оборудование или дата, то не писать

function searchData(idToSearch) {
  if (idToSearch.startsWith('moon')){
    return moonlist.filter(item => {
      return item.id === idToSearch
    })
  }
  if (idToSearch.startsWith('sun')){
    return sunlist.filter(item => {
      return item.id === idToSearch
    })
  }
  if (idToSearch.startsWith('planets')){
    return planetlist.filter(item => {
      return item.id === idToSearch
    })
  }
  };

const ImageDetails = () => {
    const { id } = useParams()
    const imageData=searchData(id)[0]
    return (
        <main className='imageinfo-main'>
            <h2>{imageData.title}</h2>
            <a href={imageData.filename+'.jpg'}><img id='image' src={imageData.filename+'.jpg'} /></a>
            <p><i>{imageData.date}</i></p>
            <p>{imageData.info}</p>
            <p><i>{imageData.instruments?'Оборудование:':''}<br/></i>{imageData.instruments}</p>
            
        </main>
    )
}

export default ImageDetails