import { Component } from "react"
import './App.css'
import Moon from "./components/moon"
import Navigation from "./components/navigation"
import Header from "./components/header"
import Footer from "./components/footer"
import Homepage from "./components/homepage"
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Sun from "./components/sun"
import Gallery from "./components/gallery"
import ImageDetails from "./components/ImageDetails"
import Planets from "./components/planets"
import Comets from "./components/comets"

//npm run deploy
/*файлы именовать без _ в начале и только маленькие буквы - гит ругается*/
//убирать менюшку после перехода по ссылкам

class App extends Component {
  render(){
      return (
        <Router>
          <main>
              <Header />
              <Navigation />
              <Switch>
                <Route exact path='/main'>
                  <Homepage />
                </Route>
                <Route path='/main/moon'>
                  <Moon />
                </Route>
                <Route path='/main/sun'>
                  <Sun />
                </Route>
                <Route path='/main/planets'>
                  <Planets />
                </Route>
                <Route path='/main/comets'>
                  <Comets />
                </Route>
                <Route path='/main/gallery'>
                  <Gallery />
                </Route>
                <Route path='/main/images/:id'>
                  <ImageDetails />
                </Route>
              </Switch>
              <Footer />
          </main>
        </Router>
      );
  }
}
export default App;
