import { Link } from 'react-router-dom';
import './gallery.css'
import ImageTile from "./image_tile"
import planetlist from './planetlist';

const Planets = () => {
    return (
        <main className='gallery_grid'>
            {planetlist.map((planetImage) => (
                <Link to={`/main/images/${planetImage.id}`}>
                    {console.log(planetImage.filename+'_min.jpg')}
                    <ImageTile filename={planetImage.filename+'_min.jpg'} title={planetImage.title} />
                </Link>
            ))}
        </main>
    );
}


export default Planets