import { Link } from 'react-router-dom'
import './gallery.css'
import ImageTile from "./image_tile"
import moonlist from './moonlist'

const Moon = () => {
    return (
        <main className='gallery_grid'>
            {moonlist.map((moonImage) => (
                <Link to={`/main/images/${moonImage.id}`}>
                    <ImageTile filename={moonImage.filename+'_min.jpg'} title={moonImage.title} />
                </Link>
            ))}
        </main>
    );
}

export default Moon