import './gallery.css'
import { Link } from 'react-router-dom'
//общая галерея

function GalleryTiles(props){
    return (
        <Link to={props.linkto}>
            <article className="gallery_item">
                    <p className="item_description"><img src={props.filename} alt=""/></p>
                    <p className="item_title">{props.title}</p>
            </article>
        </Link>
    )
}

const Gallery = () => {
    return (
        <section>
            <div className='gallery_grid'>
                <GalleryTiles linkto='/main/sun' filename='/main/images/astrophotography/sun/mg_9204_min.jpg' title='Солнце' />
                <GalleryTiles linkto='/main/moon' filename='/main/images/astrophotography/moon/fullmoon_color_min.jpg' title='Луна' />
                <GalleryTiles linkto='/main/planets' filename='/main/images/astrophotography/planets/mars_20200922_min.jpg' title='Планеты' />
                <GalleryTiles linkto='/main/comets' filename='/main/images/astrophotography/46p_min.jpg' title='Кометы' />
                <GalleryTiles linkto='/main/dso' filename='/main/images/astrophotography/and_bin_min.jpg' title='Дипскай' />
                <GalleryTiles linkto='/main/stars' filename='/main/images/astrophotography/img_1876_min.jpg' title='Звездные поля' />
                <GalleryTiles linkto='/main/sat' filename='/main/images/astrophotography/mg_1359_min.jpg' title='Искусственные спутники' />
                <GalleryTiles linkto='/main/atmospherical' filename='/main/images/astrophotography/mg_6127_min.jpg' title='Атмосферные явления' />
            </div>
        </section>
    );
}

export default Gallery