import './gallery.css'
import ImageTile from "./image_tile"
import sunlist from './sunlist'
import { Link } from 'react-router-dom'

const Sun = () => {
    return (
        <main>
            <h3>Снимки Солнца с разными увеличениями</h3>
            <section className='gallery_grid'>
            {sunlist.map((sunImage) => (
                <Link to={`/main/images/${sunImage.id}`}>
                    {console.log(sunImage.filename+'_min.jpg')}
                    <ImageTile filename={sunImage.filename+'_min.jpg'} title={sunImage.title} />
                </Link>
            ))}
            </section>
        </main>
    );
}

export default Sun