import './navigation.css'
import { Link } from 'react-router-dom'

//попробовать сделать так, чтобы менюшка возвращалась, если выключил ее на мелком экране и потом вернулся на полный

function showNav(){
    let ulState=document.querySelector('ul').style
    ulState.display==='inherit'?ulState.display='none':ulState.display='inherit'
}

const Navigation = () => {
    return (
    <nav>
        <button onClick={showNav}>РАЗДЕЛЫ</button>
        <ul>
            <li><Link to="/main/">Начальная</Link></li>
            <li><Link to="/main/gallery">Фотогалерея</Link>
            <ul>
                <li><Link to="/main/sun">Солнце</Link></li>
                <li><Link to="/main/moon">Луна</Link></li>
                <li><Link to="/main/planets">Планеты</Link></li>
                <li><Link to="/main/comets">Кометы</Link></li>
                <li><Link to="/main/dso">Дипскай</Link></li>
                <li><Link to="/main/transient">Транзиентные источники</Link></li>
                <li><Link to="/main/stars">Звездные поля</Link></li>
                <li><Link to="/main/sat">Искусственные спутники</Link></li>
                <li><Link to="/main/atmospheric">Атмосферные явления</Link></li>
            </ul>
            </li>
            <li><Link to="/videos">Видео</Link></li>
            <li><Link to="/radio">Радио</Link></li>
            <li><Link to="/articles">Статьи</Link></li>
            <li><Link to="/calc">Калькуляторы</Link></li>
            <li><Link to="/spaceweather">Солнечная активность</Link></li>
            <li><Link to="/links">Полезные ссылки</Link></li>
        </ul>
    </nav>)
}

export default Navigation